import { useState } from 'react'
import { Drawer, Button } from '@xrkmm/ui-h5'

const Demo: React.FC = () => {
  const [show, setShow] = useState(false)

  const onChange = () => setShow(!show)

  return (
    <>
      <Button onClick={onChange} type="error">
        
      </Button>
      <Drawer visible={show} onClose={onChange} maskClosable={false}>
        <div
          style={{
            width: '100%',
            height: 300,
            backgroundColor: '#fff',
            fontSize: 40,
            textAlign: 'center',
          }}
        >
          
          <Button onClick={() => setShow(false)} type="error">
            
          </Button>
        </div>
      </Drawer>
    </>
  )
}

export default Demo
