import { useState } from 'react'
import { ActionSheet, ActionSheetItem, Button } from '@xrkmm/ui-h5'

const list = ['', '']

const Demo: React.FC = () => {
  const [city, setCity] = useState('')
  const [show, setShow] = useState(false)
  const [showSub, setShowSub] = useState(false)

  const onSelect = (city: string): void => {
    setCity(city)
    setShow(false)
  }

  return (
    <>
      <Button onClick={() => setShow(true)}>: {city}</Button>
      <Button onClick={() => setShowSub(true)}>: {city}</Button>
      <ActionSheet
        title=""
        visible={show}
        onClose={() => setShow(false)}
      >
        {list.map((item) => (
          <ActionSheetItem
            key={item}
            onClick={() => onSelect(item)}
            text={item}
          />
        ))}
      </ActionSheet>

      <ActionSheet
        title=""
        subTitle=""
        visible={showSub}
        onClose={() => setShowSub(false)}
      >
        {list.map((item) => (
          <ActionSheetItem
            key={item}
            onClick={() => onSelect(item)}
            text={item}
          />
        ))}
      </ActionSheet>
    </>
  )
}

export default Demo
