import { useState } from 'react'
import { Modal, Button } from '@xrkmm/ui-h5'

const Demo: React.FC = () => {
  const [idnex, setIndex] = useState(0)
  const [show, setShow] = useState(false)

  return (
    <>
      <Button onClick={() => setShow(true)}> {idnex}</Button>
      <Modal
        visible={show}
        title=""
        content=""
        button={[
          { text: '', style: 'cancel' },
          { text: '', style: 'cancel' },
          { text: '' },
        ]}
        onButtonClick={(index) => {
          setIndex(index)
          setShow(false)
        }}
      />
    </>
  )
}

export default Demo
