import { useState } from 'react'
import { Drawer, Button } from '@xrkmm/ui-h5'

const Demo: React.FC = () => {
  const [show, setShow] = useState(false)
  const [topShow, setTopShow] = useState(false)
  const [leftShow, setLeftShow] = useState(false)
  const [rightShow, setRightShow] = useState(false)
  const [centerShow, setCenterShow] = useState(false)

  const onChange = () => setShow(!show)
  const onTopChange = () => setTopShow(!topShow)
  const onLeftChange = () => setLeftShow(!leftShow)
  const onRightChange = () => setRightShow(!rightShow)
  const onCenterChange = () => setCenterShow(!centerShow)

  return (
    <>
      <Button onClick={onChange}></Button>
      <Button onClick={onTopChange} type="warn">
        
      </Button>
      <Button onClick={onLeftChange} type="error">
        
      </Button>
      <Button onClick={onRightChange} type="pop">
        
      </Button>
      <Button onClick={onCenterChange}></Button>

      <Drawer visible={show} onClose={onChange} position="bottom">
        <div
          style={{
            width: '100%',
            height: 300,
            backgroundColor: '#fff',
            fontSize: 40,
            textAlign: 'center',
          }}
        >
          
        </div>
      </Drawer>
      <Drawer visible={topShow} onClose={onTopChange} position="top">
        <div
          style={{
            width: '100%',
            height: 300,
            backgroundColor: '#fff',
            fontSize: 40,
            textAlign: 'center',
          }}
        >
          
        </div>
      </Drawer>
      <Drawer visible={leftShow} onClose={onLeftChange} position="left">
        <div
          style={{
            width: 300,
            height: '100%',
            backgroundColor: '#fff',
            fontSize: 40,
            textAlign: 'center',
          }}
        >
          
        </div>
      </Drawer>
      <Drawer visible={rightShow} onClose={onRightChange} position="right">
        <div
          style={{
            width: 300,
            height: '100%',
            backgroundColor: '#fff',
            fontSize: 40,
            textAlign: 'center',
          }}
        >
          
        </div>
      </Drawer>
      <Drawer visible={centerShow} onClose={onCenterChange} position="center">
        <div
          style={{
            width: 300,
            height: 300,
            backgroundColor: '#fff',
            fontSize: 40,
            textAlign: 'center',
          }}
        >
          
        </div>
      </Drawer>
    </>
  )
}

export default Demo
