import { useState } from 'react'
import { Button, Toast } from '@xrkmm/ui-h5'

const Demo: React.FC = () => {
  const [show, setShow] = useState(false)

  const onClick = () => {
    Toast.show({
      title: '10taost',
      duration: 10000,
      success: () => setShow(true),
    })
  }

  const onHide = () => {
    Toast.hide()
    setShow(false)
  }

  return (
    <>
      <Button onClick={onClick}>10taost</Button>
      <Button onClick={onHide} disabled={!show}>
        toast
      </Button>
    </>
  )
}

export default Demo
