import { useState } from 'react'
import { ActionSheet, ActionSheetItem, Button } from '@xrkmm/ui-h5'

const list = ['', '']

const Demo: React.FC = () => {
  const [city, setCity] = useState('')
  const [showLeft, setShowLeft] = useState(false)
  const [showCenter, setShowCenter] = useState(false)
  const [showRight, setShowRight] = useState(false)

  const onSelect = (city: string): void => {
    setCity(city)
    setShowLeft(false)
    setShowCenter(false)
    setShowRight(false)
  }

  return (
    <>
      <Button onClick={() => setShowLeft(true)}>: {city}</Button>
      <Button onClick={() => setShowCenter(true)}>: {city}</Button>
      <Button onClick={() => setShowRight(true)}>: {city}</Button>

      <ActionSheet
        title=""
        subTitle=""
        titleAlign="left"
        visible={showLeft}
        onClose={() => setShowLeft(false)}
      >
        {list.map((item) => (
          <ActionSheetItem
            key={item}
            onClick={() => onSelect(item)}
            text={item}
          />
        ))}
      </ActionSheet>

      <ActionSheet
        title=""
        subTitle=""
        titleAlign="center"
        visible={showCenter}
        onClose={() => setShowCenter(false)}
      >
        {list.map((item) => (
          <ActionSheetItem
            key={item}
            onClick={() => onSelect(item)}
            text={item}
          />
        ))}
      </ActionSheet>

      <ActionSheet
        title=""
        subTitle=""
        titleAlign="right"
        visible={showRight}
        onClose={() => setShowRight(false)}
      >
        {list.map((item) => (
          <ActionSheetItem
            key={item}
            onClick={() => onSelect(item)}
            text={item}
          />
        ))}
      </ActionSheet>
    </>
  )
}

export default Demo
