export const DEFAULT_DURATION = 2000

export interface ToastOption {
  /**  */
  title: string
  /** image  icon */
  image?: string
  /** , success, loading, none,  none */
  icon?: 'success' | 'loading' | 'none'
  /** ,  false */
  mask?: boolean
  /**  1500ms */
  duration?: number
  /**  */
  success?(): void
  /**  */
  fail?(): void
}

export interface ToastProps extends ToastOption {
  onClose?(): void
}
