import { useState } from 'react'
import { Modal, Button } from '@xrkmm/ui-h5'

const Demo: React.FC = () => {
  const [show, setShow] = useState(false)

  return (
    <>
      <Button onClick={() => setShow(true)}>JSX</Button>
      <Modal
        visible={show}
        title=""
        button={[{ text: '', style: 'cancel' }, { text: '' }]}
        onButtonClick={() => setShow(false)}
      >
        
      </Modal>
    </>
  )
}

export default Demo
