import { useState } from 'react'
import { Drawer, Button } from '@xrkmm/ui-h5'

const Demo: React.FC = () => {
  const [show, setShow] = useState(false)

  const onChange = () => setShow(!show)

  return (
    <>
      <Button onClick={onChange} type="error">
        {show ? '()' : '()'}
      </Button>
      <Drawer visible={show} onClose={onChange} mask={false}>
        <div
          style={{
            width: '100%',
            height: 300,
            backgroundColor: '#fff',
            fontSize: 40,
            textAlign: 'center',
            boxShadow: '0px -1px 5px 1px rgba(0, 0, 0, 0.2)',
          }}
        >
          
        </div>
      </Drawer>
    </>
  )
}

export default Demo
